import { Component, OnInit } from '@angular/core';
import { TemplateInstanceBean } from './bean/template-instance.bean';
import { BusinessCenterManageHttp } from '../http/business-center-manage.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_COMMON_UPLOAD, TZB_COMMON_DOWNLOAD } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from 'primeng/primeng';
import { CommonHttpService } from '../../../http/common.http.service';
import * as _ from 'lodash';
import { CALENDAR_CN } from 'app/pages/tzb/workflow/activiti-work-flow/common/constant/config';

@Component({
  selector: 'app-template-instance-query',
  templateUrl: './template-instance-query.component.html',
  styleUrls: ['./template-instance-query.component.scss'],
  providers: [CommonHttpService]
})
export class TemplateInstanceQueryComponent implements OnInit {
  isdeletall: boolean = true;
  selectList: any[];
  ch: any = CALENDAR_CN;//创建日期的语言
  index: any;
  fileList: any;
  msgs: Message[];
  data: any;
  inpFlag: boolean = true;
  templateInstanceBean: TemplateInstanceBean = new TemplateInstanceBean()
  first: number = 0;
  templateId: any[];//编号下拉框
  tableData: any[] //表格数据
  addFlag: boolean = false;
  total: number;
  searchList: any[];//搜索条件列表
  tableList: any[];//表头列表
  templateInfo: object;
  templateTitle: string;//模板标题
  UPLOAD: string;
  loadDisplay: boolean = false;
  files: any[] = [];
  abledFlag: boolean = true;
  doFlag: string = '';//新增、修改、删除 标识
  DOWNLOAD: string = TZB_COMMON_DOWNLOAD;
  dtId: string;//模板编号
  update_item: any={};
  user: any;
  user2: any;
  orgId: string = '';
  userId: string = '';
  userName: string = '';
  orgName: string = '';
  messageMsg: string;//
  fromOperateRecodeUpdate: any;
  dimensionMaintValueList: any = [];//利率维度值
  postIdValueList: any = [];//岗位
  arrTemp: any = [];
  code: any;
  allData:any[]=[];//存储查询回来的数据
  pmsTemplate: any[];
  constructor(
    public httpService: BusinessCenterManageHttp,
    public commfunc: Commfunc,
    public confirmationService: ConfirmationService,
    public CommonHttpService: CommonHttpService
  ) {
    this.codeValues();
    this.templateInstanceBean.pageNum = 1;
    this.templateInstanceBean.pageSize = 10;
    this.templateTitle = '模板实例管理';
  }

  ngOnInit() {
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user.orgId) {
      this.orgId = this.user.orgId;
    }
    if (this.user.userId) {
      this.userId = this.user.userId;
    }
    if (this.user2.userName) {
      this.userName = this.user2.userName;
    }
    if (this.user2.orgName) {
      this.orgName = this.user2.orgName;
    }
    this.idQuery('');

    this.queryDimensionMaintValueService();//利率维度值查询
    this.queryPostById();//岗位查询
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  inpquery(event) {
    if (this.inpFlag) {
      this.inpFlag = false;
      setTimeout(() => {
        this.inpFlag = true;
        let dtId = event.target.value
        this.idQuery(dtId)
      }, 1000)
    }
  }
  //模板编号模糊查询
  idQuery(dtId) {
    let param = {
      dtId,
      pageSize: 30,
    }
    this.httpService.queryDocumentTemplates(param).subscribe(data => {
      if (data.returnCode.code == 'success' && data.result.templateInfoList) {
        this.templateId = this.commfunc.listToLabelvalue(data.result.templateInfoList, 'dtId', 'dtName');
      }
    })
  }

  //模板变更处理
  doChose(event) {
    this.templateInstanceBean.dtName = this.templateInstanceBean.dtId;
  }
  //模板编号查询
  query() {
    let dtId = '';
    if (this.templateId) {
      for (let i = 0; i < this.templateId.length; i++) {
        if (this.templateId[i].value == this.templateInstanceBean.dtId)
          dtId = this.templateId[i].label
      }
    } else {
      return;
    }
    this.httpService.queryDocumentTemplateDetails({ dtId }).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dtId = dtId;
        this.searchList = this.toDropdown(data.result.searchList);

        this.arrTemp = [];
        data.result.tableList.forEach((element, i) => {
          if (element.daField == 'preferentialAccumulationProportion' || element.daField == 'accumulationProportion' || element.daField == 'currentAccumulationProportion') {//积数比例和积分数比例和当前积数比例
            data.result.tableList[i].fileList = this.dimensionMaintValueList;
          }
          if (element.daField == 'postId') {//岗位
            let arr = [];
            this.postIdValueList.forEach(element => {
              arr.push({ fieldValue: element.label, fieldKey: element.value })
            });
            data.result.tableList[i].fileList = arr;
          }
          if (element.daType == '03') {//日期型
            this.arrTemp.push(element.daField);
          }
        });
        this.tableList = this.toDropdown(data.result.tableList);

        //console.log(this.tableList)
        this.templateInfo = data.result.templateInfo;
        this.templateTitle = this.templateInfo['dtName'];
        this.abledFlag = false;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
        this.manageQuery();
        let user = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        let path = `BusinessCenter/TempInstance/${this.templateInfo['dtId']}/${this.commfunc.transDate(new Date())}/${user}`;
        this.UPLOAD = `${TZB_COMMON_UPLOAD}?path=${path}`;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.code });
      }
    })
  }

  toDropdown(list) {
    //处理下拉框的值
    list.forEach(item => {
      if (item['fileList']) {
        let arr = [];
        item['fileList'].forEach(itemChild => {
          let obj = {};
          obj['label'] = itemChild.fieldValue;
          obj['value'] = itemChild.fieldKey;
          arr.push(obj);
        })
        item['valueList'] = JSON.parse(JSON.stringify(arr));
        item['valueList'].unshift({ label: "请选择", value: '' });
      }
    })
    return list
  }

  //利率维度值查询
  queryDimensionMaintValueService() {
    let params = {
      dmsnlChiNm: "积数比",
      pageNum: 1,
      pageSize: 10000
    }
    this.httpService.queryDimensionMaintValueService(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dimensionMaintValueList = [];
        if (data.resultList && data.resultList.legnth != 0) {
          data.resultList.forEach(element => {
            this.dimensionMaintValueList.push({ fieldKey: element.dmsnlValue, fieldValue: element.dmslValDesc });
          });
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
    });
  }

  //重置
  tempIdReset() {
    this.templateInstanceBean = new TemplateInstanceBean();
  }
  //搜索下拉框取值
  searchInfo(event, data) {
    data = event.value;
    // this.searchList.forEach((element, i) => {
    //   if (element.daType == '03') {
    //     this.searchList[i].saveValue = new Date(this.tableList[i].saveValue).getTime();
    //   }
    // });
  }
  //管理查询
  manageQuery() {
    this.doFlag = '';
    let searchInfoMap = {};
    this.searchList.forEach(item => {
      if (item['value']) {
        searchInfoMap[item.daAnotherField] = item['value'];
      }
    })
    let param = {
      dtId: this.templateInfo['dtId'],
      entityName: this.templateInfo['entityName'],
      searchInfoMap,
      pageNum: this.templateInstanceBean.pageNum,
      pageSize: this.templateInstanceBean.pageSize
    }
    this.httpService.queryPermissionInfoList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.result.permissionsInfoList) {
          this.total = data.result.total;
          this.tableData = data.result.permissionsInfoList;
          if (this.tableData.length != 0) {
            this.tableData.forEach((element, i) => {
              if (element.postId && element.postId != '') {
                this.tableData[i].valueList = this.postIdValueList;
              }
              if (this.arrTemp && this.arrTemp.length != 0) {
                this.arrTemp.forEach((element2, ind) => {
                  this.tableData[i][element2] = this.commfunc.transDateN(new Date(this.tableData[i][element2]));
                });
              }
            });
          }

          this.tableData = _.clone(this.tableData);
          this.saveData(this.tableData);
        } else {
          this.tableData = [];
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '查询失败' })
      }
    })
  }

  //查询岗位
  queryPostById() {
    let params = {
      pageNum: 1,
      pageSize: 10000
    }
    this.httpService.queryPostById(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.postIdValueList = [];
        if (data.postInfoList && data.postInfoList.legnth != 0) {
          data.postInfoList.forEach(element => {
            this.postIdValueList.push({ value: element.postId, label: element.postName });
          });
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
    });
  }

  toParam(list, value) {
    let param
    if (this.doFlag == '1' || this.doFlag == '2') {
      param = { seqNo: this.index };
    } else {
      param = {};
    }
    list.forEach(item => {
      // if (item[value]) {
      param[item.daAnotherField] = item[value];
      // }
    })
    return param;
  }
  //管理重置
  manageReset() {
    this.searchList.forEach(item => {
      item.value = null;
    })
  }
  //导入
  import() {
    this.files = [];
    this.loadDisplay = true;
  }
  load(event) {
    this.files.push(...event.files);
    if (event.xhr.response) {
      this.fileList = JSON.parse(event.xhr.response);
      this.fileList.forEach(item => {
        item['entityName'] = this.templateInfo['entityName'];
      })
      if (this.fileList.hasOwnProperty('errorMsg')) {
        this.msgs = [{ severity: 'error', summary: '提示', detail: this.fileList.errorMsg }];
      }
      this.update_item = {
        fileName: JSON.parse(event.xhr.response)[0].fileName,
        filePath: JSON.parse(event.xhr.response)[0].filePath,
        operateType: '3'
      }
      this.updateAddDelete();
      // this.httpService.fileReaderAndInsert(params).subscribe(data => {
      //   if (data.returnCode.code == 'success') {
      //     this.msgs = [{ severity: 'success', summary: '提示', detail: '上传成功！' }];
      //   }
      // })
    } else {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '获取服务器列表失败！' }];
    }
  }
  //新增
  add() {
    this.addFlag = true;
    this.doFlag = '0';
  }

  //删除
  delete(param) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        // 条件
        this.index = param['seqNo'];
        this.doFlag = '2';
        // this.save();
        if (param.valueList) {
          delete param.valueList
        }
        this.update_item = {
          fromOperateRecode: JSON.stringify(param),
          toOperateRecode: '',
          operateType: '2'
        }
        this.updateAddDelete(param, 'dele');
      }
    });
  }
  //分页
  paginate(event) {
    let rows = event.rows;
    //每页显示的条数
    this.templateInstanceBean.pageSize = rows;
    //当前页
    let currentPage = event.page + 1;
    this.templateInstanceBean.pageNum = currentPage;
    this.first = event.rows * event.page;

    //调用查询的方法
    this.manageQuery();
  }
  //下载
  download() {
    let param = {
      dtId: this.dtId,
      outCount: ''
    }
    this.httpService.downLodeDocumentTemplate(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        window.open(this.DOWNLOAD + "?fileName=" + encodeURI(data.result.fileName) + "&fileUrl=" + data.result.fileUrl, "_self")
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }
  //修改
  update(item) {
    let temp = JSON.stringify(item);
    this.fromOperateRecodeUpdate = temp;

    this.addFlag = true;
    this.doFlag = '1';
    this.index = item['seqNo'];
    let searchList = JSON.parse(JSON.stringify(this.tableList));
    for (let i in item) {
      for (let j = 0; j < searchList.length; j++) {
        if (i == searchList[j]['daAnotherField']) {
          searchList[j]['saveValue'] = item[i];
          break;
        }
      }
    }
    this.tableList = JSON.parse(JSON.stringify(searchList));

  }
  //新增修改删除
  updateAddDelete(col?, flag?, all?) {
    this.pmsTemplate = JSON.parse(JSON.stringify(this.code['PmsTemplate']));
    // console.log(this.tableList);
    // this.tableList.forEach((element, i) => {
    //   if (element.daType == '03') {
    //     this.tableList[i].saveValue = new Date(this.tableList[i].saveValue).getTime();
    //   }
    // });
    if (this.doFlag && this.doFlag == '0') {
      let toOperateRecode = JSON.stringify(this.toParam(this.tableList, 'saveValue'));
      this.update_item = {
        toOperateRecode: toOperateRecode,
        fromOperateRecode: '',
        operateType: '0'
      }
    } else if (this.doFlag == '1') {
      let toOperateRecode = JSON.stringify(this.toParam(this.tableList, 'saveValue'));
      this.update_item = {
        toOperateRecode: toOperateRecode,
        fromOperateRecode: this.fromOperateRecodeUpdate,
        operateType: '1'
      }
    }
    let item_data = this.update_item;
    let modifyInfoMap = {};
    let flagValue = '';
    if (flag == 'dele') {
      if (col.valueList) {
        delete col.valueList;
      }
      modifyInfoMap = JSON.parse(JSON.stringify(col));
      delete modifyInfoMap['createdStamp'];
      delete modifyInfoMap['createdTxStamp'];
      delete modifyInfoMap['lastUpdatedStamp'];
      delete modifyInfoMap['lastUpdatedTxStamp'];
      
      
    } else if (flag == 'deletAll') {
      this.doFlag = '2';
      if (all) {
        modifyInfoMap['deleteFlag'] = '2';
        modifyInfoMap['deleteList'] = [];
      } else {
        modifyInfoMap['deleteFlag'] = '1';
        modifyInfoMap['deleteList'] = this.selectList;
        modifyInfoMap['deleteApprovalList'] = this.matchData(this.allData,this.selectList);
        modifyInfoMap['tableList'] = this.tableList;
      }
    } else {
      modifyInfoMap = this.toParam(this.tableList, 'saveValue');
    }
    let dtIdValue = this.templateInfo['dtId'];
    let flag1 = this.checkValueData(dtIdValue, this.pmsTemplate);
      if(flag1){
        flagValue = 'SAVE';
      }else{
        flagValue = 'SUBMIT';
      }

    let param = {
      doFlag: this.doFlag,
      dtId: this.templateInfo['dtId'],
      entityName: this.templateInfo['entityName'],
      modifyInfoMap,
      externalIdType: 'PERMISSION',
      fromOperateRecode: item_data.fromOperateRecode || '',
      toOperateRecode: item_data.toOperateRecode || '',
      flag: flagValue,
      tellerId: this.userId,
      tellerName: this.userName,
      orgId: this.orgId,
      orgName: this.orgName,
      operateType: item_data.operateType || '2',
      fileName: item_data.fileName || '',
      filePath: item_data.filePath || '',
    }
    this.httpService.createOrUpdatePermissionChangeReq(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        if (param.operateType == '0') {
          this.messageMsg = '新增提交审批成功';
          this.addFlag = false;
        } else if (param.operateType == '1') {
          this.messageMsg = '修改提交审批成功';
          this.addFlag = false;
        } else if (param.operateType == '2') {
          this.messageMsg = '删除提交审批成功';
        } else if (param.operateType == '3') {
          this.messageMsg = '导入提交审批成功';
        }
        this.manageQuery();
        this.msgs.push({ severity: 'success', summary: '提示', detail: this.messageMsg })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' })
    })
  }

  checkValueData(dtIdValue,pmsTemplate){
    let flag = false;
    if(pmsTemplate.length > 0){
      for (let j = 0; j < pmsTemplate.length; j++) {
        if (dtIdValue == pmsTemplate[j]['value']) {
          flag = true;
          return flag;
        }
     }
    }
    return flag;
  }

  //匹配选中的数据
  matchData(list,childList){
    let newList = [];
   childList.forEach(item => {
     for(let i=0;i<list.length;i++){
       if(item == list[i].seqNo){
         newList.push(list[i]);
         break;
       }
     }
   });
    return newList
  }

  //存储查询回来的数据
  saveData(list){
    list.forEach(item => {
      let flag = true;
      for(let i=0;i<this.allData.length;i++){
        if(item.seqNo == this.allData[i].seqNo){
          flag = false;
          break;
        }
      }
      if(flag){
        this.allData.push(item);
      }
    });
  }
  //清除所有
  removeAll() {
    this.updateAddDelete('', 'deletAll', 'all');
  }

  select(event, item) {
    console.log(this.selectList);
    if(this.selectList&&this.selectList.length>0){
      this.isdeletall = false;
    }else{
      this.isdeletall = true;
    }
  }
  
  remove(){
    if (!(this.selectList && this.selectList.length > 0)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '请至少勾选一个' }];
      return
    }
    this.updateAddDelete('', 'deletAll', '');
  }
}
